<template><section><html><head></head><body><h1>Cesium3DTileset</h1>
<p><code v-pre="">vc-primitive-tileset</code> 组件用于加载 3DTiles 模型。</p>
<h2>示例</h2>
<h3>加载 3DTileset 图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-tileset :url="url" @ready-promise="readyPromise" @initial-tiles-loaded="initialTilesLoaded" @all-tiles-loaded="allTilesLoaded" @load-progress="loadProgress" @tile-failed="tileFailed" @tile-load="tileLoad" @tile-unload="tileUnload" @tile-visible="tileVisible" @click="clicked">
        </vc-primitive-tileset>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-tileset</span>
        <span class="hljs-attr">:url</span>=<span class="hljs-string">"url"</span>
        <span class="hljs-attr">@ready-promise</span>=<span class="hljs-string">"readyPromise"</span>
        <span class="hljs-attr">@initial-tiles-loaded</span>=<span class="hljs-string">"initialTilesLoaded"</span>
        <span class="hljs-attr">@all-tiles-loaded</span>=<span class="hljs-string">"allTilesLoaded"</span>
        <span class="hljs-attr">@load-progress</span>=<span class="hljs-string">"loadProgress"</span>
        <span class="hljs-attr">@tile-failed</span>=<span class="hljs-string">"tileFailed"</span>
        <span class="hljs-attr">@tile-load</span>=<span class="hljs-string">"tileLoad"</span>
        <span class="hljs-attr">@tile-unload</span>=<span class="hljs-string">"tileUnload"</span>
        <span class="hljs-attr">@tile-visible</span>=<span class="hljs-string">"tileVisible"</span>
        <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>
      &gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-tileset</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">url</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/SampleData/Cesium3DTiles/Tilesets/dayanta/tileset.json'</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">clicked</span>(<span class="hljs-params">a</span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(a)
      },
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span> = cesiumInstance
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
      },
      <span class="hljs-title function_">readyPromise</span>(<span class="hljs-params">tileset</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
        viewer.<span class="hljs-title function_">zoomTo</span>(tileset, <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">HeadingPitchRange</span>(<span class="hljs-number">0.0</span>, -<span class="hljs-number">0.5</span>, tileset.<span class="hljs-property">boundingSphere</span>.<span class="hljs-property">radius</span> * <span class="hljs-number">2.0</span>))
      },
      <span class="hljs-title function_">allTilesLoaded</span>(<span class="hljs-params"></span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'All tiles are loaded'</span>)
      },
      <span class="hljs-title function_">initialTilesLoaded</span>(<span class="hljs-params"></span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Initial tiles are loaded'</span>)
      },
      <span class="hljs-title function_">loadProgress</span>(<span class="hljs-params">numberOfPendingRequests, numberOfTilesProcessing</span>) {
        <span class="hljs-keyword">if</span> (numberOfPendingRequests === <span class="hljs-number">0</span> &amp;&amp; numberOfTilesProcessing === <span class="hljs-number">0</span>) {
          <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Stopped loading'</span>)
          <span class="hljs-keyword">return</span>
        }

        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Loading: requests: '</span> + numberOfPendingRequests + <span class="hljs-string">', processing: '</span> + numberOfTilesProcessing)
      },
      <span class="hljs-title function_">tileFailed</span>(<span class="hljs-params">error</span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'An error occurred loading tile: '</span> + error.<span class="hljs-property">url</span>)
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Error: '</span> + error.<span class="hljs-property">message</span>)
      },
      <span class="hljs-title function_">tileLoad</span>(<span class="hljs-params">tile</span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'A tile was loaded.'</span>)
      },
      <span class="hljs-title function_">tileUnload</span>(<span class="hljs-params">tile</span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'A tile was unloaded from the cache.'</span>)
      },
      <span class="hljs-title function_">tileVisible</span>(<span class="hljs-params">tile</span>) {}
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>tileset json 文件地址。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>是否显示 tileset 模型。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Matrix4</td>
<td><code v-pre="">Matrix4.IDENTITY</code></td>
<td><code v-pre="">optional</code> 一个 4x4 变换矩阵，用于转换 tileset 的根块。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> 确定 tileset 是否投射或接收来自每个光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4</strong></td>
</tr>
<tr>
<td>maximumScreenSpaceError</td>
<td>Number</td>
<td><code v-pre="">16</code></td>
<td><code v-pre="">optional</code> 用于驱动细节细化级别的最大屏幕空间错误。</td>
</tr>
<tr>
<td>maximumMemoryUsage</td>
<td>Number</td>
<td><code v-pre="">512</code></td>
<td><code v-pre="">optional</code> tileset 可以使用的最大内存量（MB）。</td>
</tr>
<tr>
<td>cullWithChildrenBounds</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 优化选项。 是否使用子绑定卷的并集来剔除切片。</td>
</tr>
<tr>
<td>cullRequestsWhileMoving</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 优化选项。 请勿请求由于相机的移动而可能回来的未使用的图块。 此优化仅适用于固定图块集。</td>
</tr>
<tr>
<td>cullRequestsWhileMovingMultiplier</td>
<td>Number</td>
<td><code v-pre="">60.0</code></td>
<td><code v-pre="">optional</code> 优化选项。 移动时用于剔除请求的乘数。 较大的代表更积极的剔除，较小的代表较不积极的剔除。</td>
</tr>
<tr>
<td>preloadWhenHidden</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> tileet.show为false时预加载图块。 加载图块时，好像图块集可见但不渲染它们。</td>
</tr>
<tr>
<td>preloadFlightDestinations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 优化选项。 在相机飞行过程中是否预加载瓦片。</td>
</tr>
<tr>
<td>preferLeaves</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 优化选项。 是否优先加载第一层级。</td>
</tr>
<tr>
<td>dynamicScreenSpaceError</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 优化选项。 减少远离相机的瓷砖的屏幕空间错误。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorDensity</td>
<td>Number</td>
<td><code v-pre="">0.00278</code></td>
<td><code v-pre="">optional</code> 用于调整动态屏幕空间误差密度，类似于雾密度。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorFactor</td>
<td>Number</td>
<td><code v-pre="">4.0</code></td>
<td><code v-pre="">optional</code> 用于增加计算的动态屏幕空间错误的因子。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorHeightFalloff</td>
<td>Number</td>
<td><code v-pre="">0.25</code></td>
<td><code v-pre="">optional</code> tileset 高度开始下降的比率。</td>
</tr>
<tr>
<td>progressiveResolutionHeightFraction</td>
<td>Number</td>
<td><code v-pre="">0.3</code></td>
<td><code v-pre="">optional</code> 优化选项。 如果介于（0.0，0.5]之间，则将优先考虑屏幕空间错误或高于屏幕空间错误的图块，以降低ProgressiveResolutionHeightFraction * screenHeight的屏幕分辨率，这有助于在继续加载全分辨率图块的同时快速缩小图块层。</td>
</tr>
<tr>
<td>foveatedScreenSpaceError</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 优化选项。 通过暂时提高屏幕边缘周围的图块的屏幕空间错误，可以优先在屏幕中央加载图块。 加载由Cesium3DTileset#foveatedConeSize确定的屏幕中心的所有图块后，屏幕空间错误将恢复正常。</td>
</tr>
<tr>
<td>foveatedConeSize</td>
<td>Number</td>
<td><code v-pre="">0.1</code></td>
<td><code v-pre="">optional</code> 优化选项。 当Cesium3DTileset#foveatedScreenSpaceError为true时使用，以控制确定延迟哪些图块的圆锥体大小。 此圆锥体内部的瓷砖会立即加载。 圆锥体外部的图块可能会根据其在圆锥体外部的距离和其屏幕空间误差而推迟。 这由Cesium3DTileset#foveatedInterpolationCallback和Cesium3DTileset#foveatedMinimumScreenSpaceErrorRelaxation控制。 将该值设置为0.0表示圆锥将是由相机位置及其视图方向形成的线。 将此值设置为1.0意味着圆锥体将覆盖相机的整个视场，从而禁用效果。</td>
</tr>
<tr>
<td>foveatedMinimumScreenSpaceErrorRelaxation</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 优化选项。 当Cesium3DTileset#foveatedScreenSpaceError为true时使用，以控制凹入圆锥体外部的图块的起始屏幕空间误差松弛。 屏幕空间错误将根据提供的Cesium3DTileset#foveatedInterpolationCallback，从图块值开始直至Cesium3DTileset＃maximumScreenSpaceError引发。</td>
</tr>
<tr>
<td>foveatedInterpolationCallback</td>
<td>Function</td>
<td></td>
<td><code v-pre="">optional</code> 优化选项。 当Cesium3DTileset#foveatedScreenSpaceError为true时使用，以控制在凹入圆锥体外部的图块引发屏幕空间误差的程度，并在Cesium3DTileset#foveatedMinimumScreenSpaceErrorRelaxation和Cesium3DTileset#maximumScreenSpaceError之间进行插值</td>
</tr>
<tr>
<td>foveatedTimeDelay</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> 优化选项。 当Cesium3DTileset#foveatedScreenSpaceError为true时使用，用于控制相机停止移动之后要等待多长时间（秒）才开始加载延迟的图块。此时间延迟可防止在相机移动时在屏幕边缘周围请求图块。 将此设置为0.0将立即请求任何给定视图中的所有图块。</td>
</tr>
<tr>
<td>skipLevelOfDetail</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 优化选项。 确定在遍历期间是否应该应用详细信息跳过级别。</td>
</tr>
<tr>
<td>baseScreenSpaceError</td>
<td>Number</td>
<td>1024</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，在跳过详细级别之前必须达到的屏幕空间错误。</td>
</tr>
<tr>
<td>skipScreenSpaceErrorFactor</td>
<td>Number</td>
<td>16</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，定义要跳过的最小屏幕空间错误的乘数。 与 skipLevels 结合使用以确定要加载的切片。</td>
</tr>
<tr>
<td>skipLevels</td>
<td>Number</td>
<td>1</td>
<td><code v-pre="">optional</code>当 skipLevelOfDetail 为 true 时，一个常量定义加载切片时要跳过的最小级别数。 当它为 0 时，不会跳过任何级别。与 skipScreenSpaceErrorFactor 结合使用以确定要加载的切片。</td>
</tr>
<tr>
<td>immediatelyLoadDesiredLevelOfDetail</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，将只下载满足最大屏幕空间错误的切片。跳过因子将被忽略，并且只加载所需的切片。</td>
</tr>
<tr>
<td>loadSiblings</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，确定在遍历期间是否始终下载可见切片的兄弟节点。</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>ClippingPlaneCollection</td>
<td></td>
<td><code v-pre="">optional</code> ClippingPlaneCollection 用于有选择地禁用渲染 tileset。</td>
</tr>
<tr>
<td>classificationType</td>
<td>ClassificationType</td>
<td></td>
<td><code v-pre="">optional</code> 确定此 tileset 是否会对 terrain，3D Tiles 或两者进行分类。 有关限制和限制的详细信息，请参阅 Cesium3DTileset＃classificationType。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Ellipsoid</td>
<td>Ellipsoid.WGS84</td>
<td><code v-pre="">optional</code> 决定地球的大小和形状参考椭球体。</td>
</tr>
<tr>
<td>pointCloudShading</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 用于构造 PointCloudShading 对象以基于几何误差和光照控制点衰减的选项。</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Cartesian2</td>
<td>new Cartesian2(1.0, 1.0)</td>
<td><code v-pre="">optional</code> 地球、天空、大气层的光照缩放因子。</td>
</tr>
<tr>
<td>lightColor</td>
<td>Cartesian3</td>
<td></td>
<td><code v-pre="">optional</code> 模型阴影的颜色和强度。</td>
</tr>
<tr>
<td>luminanceAtZenith</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> 太阳在天顶的亮度，以每平方米千坎德拉为单位，用于该模型的过程环境图。</td>
</tr>
<tr>
<td>sphericalHarmonicCoefficients</td>
<td>Array</td>
<td><code v-pre="">optional</code> 用于基于图像的照明的漫反射颜色的三阶球面谐波系数。</td>
<td></td>
</tr>
<tr>
<td>specularEnvironmentMaps</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> KTX文件的URL，其中包含镜面照明的立方体贴图和卷积的镜面mipmap。</td>
</tr>
<tr>
<td>backFaceCulling</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td></td>
</tr>
<tr>
<td>debugHeatmapTilePropertyName</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> tile变量以着色为热图。 所有渲染的图块将相对于彼此的指定变量值进行着色。</td>
</tr>
<tr>
<td>debugFreezeFrame</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，确定是否只使用最后一帧的切片进行渲染。</td>
</tr>
<tr>
<td>debugColorizeTiles</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则给每个 tile 一个随机颜色。</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用， 如果为 ture，则渲染每个 tile content 为线框。</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile 的边界体积。</td>
</tr>
<tr>
<td>debugShowContentBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile content 的边界体积。</td>
</tr>
<tr>
<td>debugShowViewerRequestVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile 的请求量。</td>
</tr>
<tr>
<td>debugShowGeometricError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的几何误差。</td>
</tr>
<tr>
<td>debugShowRenderingStatistics</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签以表示每个 tile 的 commonds、points、triangles、features 的数量。</td>
</tr>
<tr>
<td>debugShowMemoryUsage</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的纹理和几何内存，以 mb 为单位。</td>
</tr>
<tr>
<td>debugShowUrl</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的网址。</td>
</tr>
</tbody>
</table>
